  font-family: "macondo_swash_capsregular";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format("woff2"),
    url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format("woff");
  font-weight: normal;
  font-style: normal;
}

*:not(dialog) {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  font-family: "macondo_swash_capsregular", serif;
  background-color: #eee;
  background-image: linear-gradient(
      0deg,
      #00bcd4 25%,
      transparent 25%,
      transparent 45%,
      #00bcd4 45%,
      #00bcd4
    ),
    linear-gradient(
      90deg,
      #10aae5 25%,
      transparent 25%,
      transparent 45%,
      #10aae5 45%,
      #00bcd4
    );
  background-size: 50px 50px;
  background-position: 0 0, 50px 50px;
  color: white;

  main {
    margin-top: 5vh;
  }
}

#landing-dialog::before {
  content: "";
  align-self: center;
  justify-self: center;
  grid-row: 1;
  grid-column: 1;
  width: clamp(250px, 450px, min(85vh, 85vw));
  height: clamp(250px, 450px, min(85vw, 85vh));

  aspect-ratio: 1;
  min-width: 150px;

  border-radius: 50%;
  background: repeating-radial-gradient(
    circle at 50% 50%,
    #d4ffb382 0%,
    #d4ffb382 0.7%,
    transparent 0.7%,
    transparent 17.5%
  );
  background-position: 0px, 0px;
  /*position: absolute;
top: calc(50px + 1vh);*/
  padding: 20px;
}
#circle {
  grid-row: 1;
  grid-column: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 15px;
  padding: 30px;
  width: clamp(300px, 550px, min(95vh, 95vw));
  height: clamp(300px, 550px, min(95vw, 95vh));
  aspect-ratio: 1;
  min-width: 150px;

  border-radius: 50%;
  background: repeating-conic-gradient(
    from 0.25turn at 50% 50%,
    #f69d3ce6,
    10deg,
    #0a762c99,
    350deg,
    #d4ffb3ba
  );
  header {
    margin-top: calc(calc(min(100vh, 100vw) - 100%) / 2 - 25%);
  }

  form {
    z-index: 2;
    max-width: 85%;
    font-size: clamp(14px, 3vw, 1.8rem);
    display: flex;
    flex-direction: column;
    gap: 15px;
    & > label:last-of-type {
      input {
        font-size: clamp(14px, 3vw, 1.8rem);
        padding: 3px;

        margin-left: 1rem;
      }
    }
  }
}

h1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: clamp(14px, 4vw, 3.5rem);
}
h1 > em {
  font-size: clamp(18px, 6vw, 4.5rem);
  letter-spacing: 1px;
  font-kerning: normal;
}
dialog {
  background: transparent;
  color: white;
  border: none;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
}

fieldset {
  padding: 15px;
  gap: 15px;
  font-size: clamp(14px, 3vw, 1.8rem);
  display: flex;
  flex-direction: column;

  text-align: center;

  legend {
    font-size: clamp(12px, 1.2rem, 5vw);
  }
  label {
    display: flex;
    align-items: center;
  }

  input {
    margin: 5px;
    max-width: 150px;
  }
  input[type="radio"] {
    appearance: none;
    width: 10px;
    height: 10px;
    border: 3px solid rgb(47, 79, 47);
    transform: skew(45deg, 313deg);
    transition: 0.2s all linear;
    margin-right: 1rem;
  }
  input::placeholder {
    font-weight: lighter;
    opacity: 0.5;
  }
  input[type="text"]:invalid {
    border: 2px solid red;
  }
  input:checked {
    border: 4px solid rgb(135, 225, 135);
  }

  #another-human:checked ~ #opp-name {
    border: 3px solid rgb(135, 225, 135);
    transition: 0.2s all linear;
  }
  #another-human:not(:checked) ~ #opp-name {
    transform: scale(1, 0);
  }
  #opp-name {
    padding: 0.5rem;
    font-size: 1.5rem;
    font-weight: 600;
    border-radius: 5px;
  }
  label:hover {
    text-shadow: 1px 0px whitesmoke;
  }
}

button {
  font-size: clamp(14px, 1.3rem, 2vw);
  padding: 10px 0px;
  border-radius: 5px;
  font-family: "Courier New", Courier, monospace;
  font-weight: 600;
  font-variant-ligatures: none;
  width: 70%;
  align-self: center;
}

button:hover,
input:hover {
  background-color: azure;
}

#main-player-grid {
  display: none;
  width: min-content;
  background-color: #00bcd4;
  h2 {
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 400;
    margin: 1px 5px;
    text-shadow: 1px 1px 1px black;
  }
}

div.label {
  text-shadow: 1px 1px 1px black;
  text-align: center;
  font-size: clamp(1rem, calc(1.1rem + 1vh), 2rem);
  font-weight: 500;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 3px ridge gray;
}
div#grid {
  width: fit-content;
  box-shadow: 1px 1px 1px 1px black;
  border-radius: 5px;
  border: 6px ridge gray;
  display: grid;
  grid-template-rows: repeat(11, max(min(4vw, 4vh), 30px));
  grid-template-columns: repeat(11, max(min(4vw, 4vh), 30px));
}

div.cell {
  border: 2px outset black;
}
.hmargin {
  margin: 4px 3px;
}
.vmargin {
  margin: 3px 4px;
}
.ship {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  border: 2px solid black;
  border-radius: 35%;
  z-index: 3;

  --s: 13px; /* control the size*/
  --c1: #b9b9b9;
  --c2: #8e8383;
  --c3: #847070;

  background: conic-gradient(
        from 75deg,
        var(--c1) 15deg,
        var(--c2) 0 30deg,
        #0000 0 180deg,
        var(--c2) 0 195deg,
        var(--c1) 0 210deg,
        #0000 0
      )
      calc(var(--s) / 2) calc(0.5 * var(--s) / tan(30deg)),
    conic-gradient(
      var(--c1) 30deg,
      var(--c3) 0 75deg,
      var(--c1) 0 90deg,
      var(--c2) 0 105deg,
      var(--c3) 0 150deg,
      var(--c2) 0 180deg,
      var(--c3) 0 210deg,
      var(--c1) 0 256deg,
      var(--c2) 0 270deg,
      var(--c1) 0 286deg,
      var(--c2) 0 331deg,
      var(--c3) 0
    );
  background-size: var(--s) calc(var(--s) / tan(30deg));
}

.destroyer {
  background-color: blue;
}
.battleship {
  background-color: #9dde9d;
}
.patrol-boat {
  background-color: pink;
}
.carrier {
  background-color: purple;
}
.submarine {
  background-color: yellow;
}

.ship-anchor {
  fill: white;
  align-self: center;
  justify-self: center;
}
